// @flow
import React from 'react';
import { Pager } from 'react-bootstrap';
import Select from 'react-select';

type Props = {
  className: string,
  currentPosition: number,
  lastPosition: number,
  navSelectOptions: Array<{ label: number, value: number }>,
  handleChange: (value: number) => void,
  moveLeft: () => void,
  moveFirst: () => void,
  moveRight: () => void,
  moveLast: () => void,
}

const NavigationPager = ({ className, currentPosition, lastPosition, navSelectOptions, handleChange, moveLeft, moveFirst, moveRight, moveLast }: Props) => (
  <Pager>
    <Pager.Item onSelect={moveFirst}>&larr;&larr;</Pager.Item>
    <Pager.Item onSelect={moveLeft}>&larr;</Pager.Item>
    <li>
      <span>
        <Select
          className={className}
          name="select-page"
          value={currentPosition}
          onChange={handleChange}
          options={navSelectOptions}
          clearable={false}
          autoBlur
          simpleValue
          noResultsText="No valid page"
        />
        <span style={{ verticalAlign: 'super' }}>{ ` of ${lastPosition} ` }</span>
      </span>
    </li>
    <Pager.Item onSelect={moveRight}>&rarr;</Pager.Item>
    <Pager.Item onSelect={moveLast}>&rarr;&rarr;</Pager.Item>
  </Pager>
);

export default NavigationPager;



