// @flow
import React from 'react';
import { ProgressBar } from 'react-bootstrap';

type Props = {
  images: {
    [img_id: number] : ImgCacheElement,
  },
}

const PreloadProgressBars = ({ images }: Props) => (
  <div style={{position: 'absolute', left: '85%', top: '16%', width: '15%'}}>
    {
      Object.keys(images).map(x => {
        const percent = images[Number(x)].percent;
        switch (images[Number(x)].state) {
          case 'progress':
            return <ProgressBar key={x} bsStyle={percent < 0 ? "warning" : "success"} now={percent}
                                label={`${x}: ${percent}%`}/>;
          case 'load':
            return <ProgressBar key={x} bsStyle="info" now={percent} label={`${x}: ${percent}%`}/>;
          case 'error':
            return <ProgressBar key={x} bsStyle="danger" now={percent} label={`${x}: ${percent}%`}/>;
          case 'abort':
            return <ProgressBar key={x} bsStyle="danger" now={percent} label={`${x}: ${percent}%`}/>;
          default:
            return <ProgressBar key={x} bsStyle="info" now={percent} label={`${x}: ${percent}%`}/>;
        }
      })
    }
  </div>
);

export default PreloadProgressBars;



